import React, {useState} from 'react'
import Affairs from './affairs/Affairs'
import s2 from '../../s1-main/App.module.css'
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import {makeStyles} from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';

/*
* 1 -   AffairPriorityType, AffairType
* 2 -     defaultAffairs
* 3 -      filterAffairs    
* 4 -   3   deleteAffair
* 5 -     useState  affairs
* 6 -      deleteAffairCallback
* 7 -   Affairs.tsx   
* 8 -   Affairs.tsx    setAll, setHigh, setMiddle, setLow
* 9 -   Affair.tsx   
* 10 -   Affair.tsx   deleteCallback  
* 11 -   Affair.tsx   
* */

// types
export type AffairPriorityType = 'high' | 'low' | 'middle'
export type AffairType = {
    _id: number
    name: string
    priority: AffairPriorityType
}
export type FilterType = 'all' | AffairPriorityType

// constants
const defaultAffairs: Array<AffairType> = [
    {_id: 1, name: 'React', priority: 'high'},
    {_id: 2, name: 'anime', priority: 'low'},
    {_id: 3, name: 'games', priority: 'low'},
    {_id: 4, name: 'work', priority: 'high'},
    {_id: 5, name: 'html & css', priority: 'middle'},
]

// pure helper functions
export const filterAffairs = (affairs: Array<AffairType>, filter: FilterType): Array<AffairType> => {
    if (filter === 'all') return affairs
    else if (filter === 'low') return affairs.filter((el: AffairType) => el.priority === 'low')
    else if (filter === 'high') return affairs.filter((el: AffairType) => el.priority === 'high')
    else if (filter === 'middle') return affairs.filter((el: AffairType) => el.priority === 'middle')

    return affairs
}
export const deleteAffair = (affairs: Array<AffairType>, _id: number): Array<AffairType> => {
    return affairs.filter((el: AffairType) => el._id !== _id)
}

const useStyles = makeStyles({
    root: {
        borderTop: '1px solid #D9D9D9',
        borderBottom: '1px solid #D9D9D9',
        padding: '24px 0 32px 0'
    },
    title: {
        margin: '39px 0 10px 70px',
        fontWeight: 600,
        fontSize: '22px',
        lineHeight: '27px'
    },

})

function HW2() {
    const classes = useStyles();
    const [affairs, setAffairs] = useState<Array<AffairType>>(defaultAffairs)
    const [filter, setFilter] = useState<FilterType>('all')

    const filteredAffairs = filterAffairs(affairs, filter)
    const deleteAffairCallback = (_id: number) => {

        setAffairs(deleteAffair(affairs, _id))
    }

    return (
        <Grid id={'hw2'}>
            <Typography className={classes.title}>Homework 2</Typography>
            <Grid className={classes.root}>
                <Container maxWidth={'lg'}>
                <Affairs
                    data={filteredAffairs}
                    setFilter={setFilter}
                    deleteAffairCallback={deleteAffairCallback}
                    filter={filter}
                />
                </Container>
            </Grid>
        </Grid>
    )
}

export default HW2
