import React, {ButtonHTMLAttributes, DetailedHTMLProps} from 'react'
import s from './SuperButton.module.css'

//    , children        
type DefaultButtonPropsType = DetailedHTMLProps<ButtonHTMLAttributes<HTMLButtonElement>,
    HTMLButtonElement>

type SuperButtonPropsType = DefaultButtonPropsType & {
    xType?: string
}

const SuperButton: React.FC<SuperButtonPropsType> = (
    {
        xType,
        className,
        disabled,
        ...restProps //       restProps,    children
    }
) => {

    const finalClassName = ` ${s.button} + ${(disabled ? s.disabled : xType === 'red' ? s.red + (className ?  '' + className : '') : s.default)}`
    //const finalClassName = s.button + (disabled ? s.red : xType === 'red' ? disabled + (s.secondary ? ' ' + className : '') : '')//    
     // const finalClassName = s.button + (disabled ? s.disabled : xType === 'red' ? s.red : xType === 'secondary') //    

    return (
        <button
            disabled={disabled}
            className={finalClassName}
            {...restProps} //        (children  )
        />
    )
}

export default SuperButton
