import React, {useState} from 'react'
import {homeWorkReducer} from './bll/homeWorkReducer'
import s from './HW8.module.css'
import s2 from '../../s1-main/App.module.css'
import SuperButton from '../hw04/common/c2-SuperButton/SuperButton'
import User from './User'
import {makeStyles} from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';

/*
* 1 -     (  ,   ) homeWorkReducer,  
* 2 -   User
* 3 -      
* */

export type UserType = {
    _id: number
    name: string
    age: number
}

export type InitialStateType = typeof initialPeople
export type  actionsType = sortUpType | sortDownType | checkAdultHoodType
export type sortUpType = { type: 'sort', payload: 'up' }
export type sortDownType = { type: 'sort', payload: 'down' }
export type checkAdultHoodType = { type: 'check', payload: number }

const initialPeople: UserType[] = [
    //    // , _id   
    {_id: 0, name: '', age: 3},
    {_id: 1, name: '', age: 66},
    {_id: 2, name: '', age: 16},
    {_id: 3, name: '', age: 44},
    {_id: 4, name: '', age: 40},
    {_id: 5, name: '', age: 55},
]

const useStyles = makeStyles({
    root: {
        borderTop: '1px solid #D9D9D9',
        borderBottom: '1px solid #D9D9D9',
        padding: '24px 0 32px 0'
    },
    title: {
        margin: '39px 0 10px 70px',
        fontWeight: 600,
        fontSize: '22px',
        lineHeight: '27px'
    },

})

const HW8 = () => {
    const [people, setPeople] = useState<UserType[]>(initialPeople)
    const [currentSort, setCurrentSort] = useState('')

    const finalPeople = people.map((u: UserType) => <User key={u._id} u={u}/>)

    const sortUp = () => {
        setPeople(
            homeWorkReducer(initialPeople, {type: 'sort', payload: 'up'})
        ) //    a.name > b.name
        setCurrentSort('up')
    }

    const sortDown = () => {
        setPeople(
            homeWorkReducer(initialPeople, {type: 'sort', payload: 'down'})
        ) //    a.name < b.name}
        setCurrentSort('down')
    }
    const check18 = () => {
        setPeople(
            homeWorkReducer(initialPeople, {type: 'check', payload: 18})
        ) // 
        setCurrentSort('18')
    }
    const classes = useStyles();

    return (
        <Grid id={'hw3'}>
            <Typography className={classes.title}>Homework 8</Typography>
            <Grid className={classes.root}>
                <Container maxWidth={'lg'}>
                    <Grid className={s.buttonsContainer}>
                        <SuperButton
                            id={'hw8-button-up'}
                            onClick={sortUp}
                            xType={currentSort === 'up' ? '' : 'secondary'}
                        >
                            Sort up
                        </SuperButton>
                        <SuperButton
                            id={'hw8-button-down'}
                            onClick={sortDown}
                            xType={currentSort === 'down' ? '' : 'secondary'}
                        >
                            Sort down
                        </SuperButton>
                        <SuperButton
                            id={'hw8-button-18'}
                            onClick={check18}
                            xType={currentSort === '18' ? '' : 'secondary'}
                        >
                            Check 18+
                        </SuperButton>
                    </Grid>

                    <table id={'hw8-users'} className={s.users}>
                        <thead className={s.thead}>
                        <tr className={s.root}>
                            <td className={s.nameCol}>Name</td>
                            <td className={s.ageCol}>Age</td>
                        </tr>
                        </thead>
                        <tbody>{finalPeople}</tbody>
                    </table>
                </Container>
            </Grid>
        </Grid>
    )
}

export default HW8
